import { NgModule, CUSTOM_ELEMENTS_SCHEMA, NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { NoteComponent } from './modules/note/note.component';
import { TextComponent } from './components/text/text.component';
import { HtmlComponent } from './components/html/html.component';
import { ImageComponent } from './components/image/image.component';
import { QuillModule } from 'ngx-quill';
import { SanitizeHtmlPipe } from './pipes/sanitize-html.pipe';
import { NgxUsefulSwiperModule } from 'ngx-useful-swiper';
@NgModule({
  declarations: [NoteComponent, TextComponent, HtmlComponent, ImageComponent, SanitizeHtmlPipe],
  imports: [CommonModule, NgxUsefulSwiperModule, QuillModule.forRoot({
    modules: {
        syntax: true,
        formula: true, // Include formula module
        // toolbar: [['formula', 'link']]  // Include button in toolbar
        toolbar: [
            ['bold', 'italic', 'underline', 'strike'], // toggled buttons
            ['blockquote', 'code-block'],

            [{ header: 1 }, { header: 2 }], // custom button values
            [{ list: 'ordered' }, { list: 'bullet' }],
            [{ script: 'sub' }, { script: 'super' }], // superscript/subscript
            [{ indent: '-1' }, { indent: '+1' }], // outdent/indent
            [{ direction: 'rtl' }], // text direction

            [{ size: ['small', false, 'large', 'huge'] }], // custom dropdown
            [{ header: [1, 2, 3, 4, 5, 6, false] }],

            [{ color: [] }, { background: [] }], // dropdown with defaults from theme
            [{ font: [] }],
            [{ align: [] }],

            ['clean'], // remove formatting button

            ['link', 'image', 'video'], // link and image, video
            ['formula', 'link']
        ]
    }
})],
  exports: [NoteComponent],
  entryComponents: [TextComponent, HtmlComponent, ImageComponent],
  schemas: [CUSTOM_ELEMENTS_SCHEMA, NO_ERRORS_SCHEMA]
})
export class NoteModule {}
