import {
  Injectable,
  Renderer2,
  ComponentFactoryResolver,
  RendererFactory2
} from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class CompLoaderService {
  componentRef: any;
  private renderer: Renderer2;
  private entryConteiner;

  constructor(
    private resolver: ComponentFactoryResolver,
    private rendererFactory: RendererFactory2
  ) {
    this.renderer = rendererFactory.createRenderer(null, null);
  }

  createComponent(
    entry,
    component
  ) {

    this.entryConteiner = entry;
    // const obj = JSON.stringify(data);
    // const configObj = JSON.stringify(configData);
    const factory = this.resolver.resolveComponentFactory(component);
    this.componentRef = this.entryConteiner.createComponent(factory);
    // this.componentRef.instance.quizData = data; // JSON.parse(obj);
    // this.componentRef.instance.configData = configData; // JSON.parse(configObj);
    // this.componentRef.instance.showSubmitBtn = showSubmitBtn; // JSON.parse(configObj);
    // this.componentRef.instance.type = type;
    // this.componentRef.instance.index = index;
    // this.componentRef.instance.showMarks = showMarks;
    // this.componentRef.instance.multiAttempt = attemptCount;
    // this.componentRef.instance.isInstantFb = isInstantFb;
  }

  get getComponentRef() {
    return this.componentRef;
  }

  clear() {
    this.entryConteiner.clear();
  }

  destroyComponent() {
    this.componentRef.destroy();
  }
}
