import { Component, OnInit, Input, Output, ViewChild, ViewContainerRef, AfterViewInit } from '@angular/core';
import { NoteService } from '../../services/note.service';
import { CompLoaderService } from '../../services/comp-loader.service';
import { TextComponent } from '../../components/text/text.component';
import { HtmlComponent } from '../../components/html/html.component';
import { ImageComponent } from '../../components/image/image.component';
import { EventEmitter } from '@angular/core';

@Component({
  selector: 'app-note',
  templateUrl: './note.component.html',
  styleUrls: ['./note.component.scss']
})
export class NoteComponent implements OnInit {
  currSlide: any;

  @Input() notePath;
  @Output() completed = new EventEmitter<any>();
  @ViewChild('noteTemplate', { read: ViewContainerRef, static: false })

  target: ViewContainerRef;
  public componentType = {};
  noteData;
  constructor(
    private noteService: NoteService,
    private templateLoader: CompLoaderService
  ) {
    this.componentType['text'] = TextComponent;
    this.componentType['html'] = HtmlComponent;
    this.componentType['image'] = ImageComponent;
  }

  ngOnInit() {
    console.log(this.notePath);
    this.getNoteData();
  }
  markAsComplete() {
    this.completed.emit(this.noteData.resourceId);
  }
  getNoteData() {
    this.noteData = { 
      closeBtn: this.notePath.closeBtn,
      data: [],
      resourceId: this.notePath.resourceId,
      resourceType: this.notePath.resourceType,
      tag: this.notePath.tag,
      title:this.notePath.title,
      tqId: this.notePath.tqId
    }; 
    this.noteData.data.push(JSON.parse(this.notePath.data));
  }

 // Load template according to there type
 loadComponent() {
  // Load component according to their template typed and dynamically push component in dom
  this.templateLoader.createComponent(
      this.target,
      TextComponent
  );
}
// Initialize slider plugin

}
